





















import { options } from '../teams/chart_options.js'
import CurrentMatchupInfo from './current_matchup_info.vue'

export default {
  components: {
    CurrentMatchupInfo
  },

  props: {
    currentMatchupSelected: {
      type: Boolean,
      default: false
    },

    blueSideGames: {
      type: Array,
      required: true
    },

    redSideGames: {
      type: Array,
      required: true
    },

    blueSideTeamId: {
      type: Number,
      required: true
    },

    redSideTeamId: {
      type: Number,
      required: true
    }
  },


  methods: {
    selectTeam(teamId, side) {
      this.$emit('teamSelected', { teamId, side })
    }
  }
}
