



















import Vue from 'vue'

export default Vue.extend({
  name: 'LeagueSplitSelector',

  props: {
    selectedId: {
      type: Number,
      required: false
    },

    showAllPlaceholder: {
      type: Boolean,
      default: false
    }
  },

  computed: {
    splits(): object[] {
      return this.$store.getters['leagues/splits']
    }
  },
  
  methods: {
    handleChange(e: Event) {
      this.$emit('change', parseInt(((e.target as HTMLSelectElement).value)))
    }
  }
})
