





























import TeamSelector from '../components/team_selector.vue'
import MatchHistory from './match_history.vue'

export default {
  name: 'CurrentMatchupInfo',

  components: {
    MatchHistory,
    TeamSelector
  },

  props: {
    blueSideGames: {
      type: Array,
      required: true
    },

    redSideGames: {
      type: Array,
      required: true
    },

    blueSideTeamId: {
      type: Number,
      required: true
    },

    redSideTeamId: {
      type: Number,
      required: true
    }
  },

  methods: {
    teamChanged(teamId, side) {
      this.$emit('change', teamId, side)
    }
  }
}
