















import Vue from 'vue'
import { EvProp } from './ev_prop'

export default Vue.extend({
  name: 'MatchEvTooltip',

  props: {
    evs: {
      type: Array as () => EvProp[],
      required: true
    },

    topOffset: {
      type: Number,
      required: true
    }
  },

  computed: {
    offset(): object {
      return { bottom: `${this.topOffset}px` }
    }
  },

  methods: {
    gameCount(count: number): string {
      return count > 0 ? count.toString() : 'All'
    }
  }
})
